<template>
    <div class="number-guess-list-page">
        <van-nav-bar title="开奖记录" fixed left-arrow  @click-left="goBack"/>
        <div class="result-content">
            <div class="result-item" v-for="(item,index) in list" :key="index">
                <div class="result-item-box">
                    <div class="result-item-title">{{item.name}}</div>
                    <div class="result-item-number">
                        <span v-for="(item,index) in item.number" :key="index" >{{item}}</span>
                    </div>
                    <div class="result-item-time">{{item.time}}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import { mapState } from 'vuex'


export default {
    data(){
        return{
            list:[
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
                {name:'第0001期',time:'2019-03-13 19:10:20',number:[8,2,5,3,6]},
            ]
        }
    },
    computed:{
        ...mapState(['user_info']),
    },
    created(){

    },
    mounted(){
        //获取开奖记录
        this.getResult();
    },
    methods:{
        //返回
		goBack(){
			this.$router.go(-1);
        },
        //获取开奖记录
        getResult(){
            let option = {
                token: this.user_info.token
            }
            this.http.post(this.http._HOST + 'number/result',option).then(result =>{
                this.utils.info('获取奖品列表...',result); 
                if(result.success){
                    this.list = result.data;
                }
            })   
        }
    }
}
</script>

<style lang="scss" scoped>
    .number-guess-list-page{
        width:100%;
        height:100%;
        background: #f6f6fa;
    }
    .van-nav-bar{
		// background: transparent;
		background: #fff;
	}
    .van-nav-bar__title{
		font-size:18px;
		color:#333;
	}
	.van-nav-bar .van-icon{
		font-size:20px;
		color:#333;
	}
    .result-content{
        padding:66px 0 25px;
        background: #f6f6fa;
    }
    .result-item{
        padding:0 15px 10px;
    }
    .result-item-box{
        background: #fff;
        border-radius: 5px;
        padding:10px;
    }
    .result-item-title{
        font-size:16px;
        color:#333;
    }
    .result-item-time{
        font-size:12px;
        color:#999;
        text-align: right;
    }
    .result-item-number{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding:16px 15px 10px;
    }
    .result-item-number span{
        display: inline-block;
        width:40px;
        height:40px;
        line-height: 40px;
        text-align: center;
        font-size:20px;
        color:#fff;
        background: #FF9465;
        border-radius: 50%;
    }
</style>


